import { state } from "./state";

export const mutations={
    addAanShowPetsData(state,data){
        let datas=null;
        for(let i=0;i<data.length;i++){
            datas=data[i].pettype;
            state.petsStyleData[datas].petsMaxData.push(data[i]);
           
        }
        
    },
    changShowType(state,data){//changShowType
        state.type=data;
    },
    changStylePage(state,data){ 
        state.petsStyleData[data.num].addPage=data.page;
    },
    isActiveDAta(state,data){
        state.petsStyleData[data.num].isActive=data.flag;
    },
    toggTabClassData(state,data){
        state.tabClassData=data;
    },
    //编辑修改社区数据方法========
    //在选择的帖子里添加评论数据
    addCommentData(state,data){
          //获取本地存储数据
          let localSData=localStorage.getItem('pestData');
          localSData=JSON.parse(localSData);
      if(localSData['communityData'][data.type][data.number].id!=undefined){
        state.communityData[data.type][data.number].commentNumberContent.unshift(data.objContent);
        localSData['communityData']["myPostData"][data.number].commentNumberContent.unshift(data.objContent);
        localSData['communityData']['newPostData'][data.number].commentNumberContent.unshift(data.objContent);
      }else{
        state.communityData[data.type][data.number].commentNumberContent.unshift(data.objContent);
        localSData['communityData'][data.type][data.number].commentNumberContent.unshift(data.objContent);
      }
        localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //删除用户评论数据
    removeCommentData(state,data){
         //获取本地存储数据
         let localSData=localStorage.getItem('pestData');
         localSData=JSON.parse(localSData);
         if(localSData['communityData'][data.type][data.number].id!=undefined){
            state.communityData[data.type][data.number].commentNumberContent.splice(data.index,1);
            localSData['communityData']["myPostData"][data.number].commentNumberContent.splice(data.index,1);
            localSData['communityData']['newPostData'][data.number].commentNumberContent.splice(data.index,1);
          }else{
            state.communityData[data.type][data.number].commentNumberContent.splice(data.index,1);
            localSData['communityData'][data.type][data.number].commentNumberContent.splice(data.index,1);
          }
       
          localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //添加发布帖子
    addPostContentData(state,data){
         //获取本地存储数据
         let localSData=localStorage.getItem('pestData');
         localSData=JSON.parse(localSData);
        state.communityData[data.type].unshift(data.objContent);
        localSData["communityData"][data.type].unshift(data.objContent);

        localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //累加收藏人数
    addCollectionNumber(state,data){
            //获取本地存储数据
            let localSData=localStorage.getItem('pestData');
            localSData=JSON.parse(localSData);
           
        state.communityData[data.type][data.index].CollectionNumber=state.communityData[data.type][data.index].CollectionNumber+1;
        state.communityData[data.type][data.index].uerIsCollection=true;

        localSData["communityData"][data.type][data.index]=state.communityData[data.type][data.index];

        localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //添加收藏帖子数据
    addmyLikePostData(state,data){
        state.myLikePostData.unshift(data);
           //获取本地存储数据
        let localSData=localStorage.getItem('pestData');
        localSData=JSON.parse(localSData);
        localSData["myLikePostData"].unshift(data);
        localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //删除收藏帖子
    removemyLikePostData(state,data){
            //获取本地存储数据
            let localSData=localStorage.getItem('pestData');
            localSData=JSON.parse(localSData);
            localSData['myLikePostData'].splice(data.index,1);

        state.myLikePostData.splice(data.index,1);
        let addNum=0;
        state.communityData[data.type].forEach(v=>{
            if(v.userNick==data.Nick){
                state.communityData[data.type][addNum].uerIsCollection=false;
                state.communityData[data.type][addNum].CollectionNumber=state.communityData[data.type][addNum].CollectionNumber-1;
            }
            addNum++;
        });
        let addNumber=0;
        localSData['communityData'][data.type].forEach(v=>{
            if(v.userNick==data.Nick){
                localSData['communityData'][data.type][addNumber].uerIsCollection=false;
                localSData['communityData'][data.type][addNumber].CollectionNumber=localSData['communityData'][data.type][addNumber].CollectionNumber-1;
            }
            addNumber++;
        })
        localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //添加宠物收藏数据
    addmyLikeData(state,data){
        
            //获取本地存储数据
            let localSData=localStorage.getItem('pestData');
            localSData=JSON.parse(localSData);
            data.obj.listType=data.listT;
            data.obj.saveAddNum=data.add
        state.myLikeData.push(data.obj);
        localSData['myLikeData'].push(data.obj);
        localStorage.setItem('pestData',JSON.stringify(localSData));
    },
    //删除宠物收藏数据
    removemyLikeData(state,index){
          //获取本地存储数据
          let localSData=localStorage.getItem('pestData');
          localSData=JSON.parse(localSData);
        state.myLikeData.splice(index,1);
        localSData['myLikeData'].splice(index,1);
        localStorage.setItem('pestData',JSON.stringify(localSData));

    },
    //删除搜索存放宠物数据
    removeMySearchData(state,index){
        state.mySearchData.splice(index,1);
    },
    //添加搜索存放宠物数据
    addMySearchData(state,data){
        let flag=true;
           //获取本地存储数据
        let localSData=localStorage.getItem('pestData');
        localSData=JSON.parse(localSData);

        state.mySearchData.forEach(v=>{
            if(v.name==data.name){
                
                flag=false;
            }
        })
        
        if(flag){
            state.mySearchData.push(data);
            localSData['mySearchData'].push(data);
            localStorage.setItem('pestData',JSON.stringify(localSData));
            // 
        }   
        
    },
    //修改显示搜索模块
    changeShowOffData(state,data){
        state.showOffData=data;
    },
    //修改宠物数据中是否收藏
    changeLike(state,data){
        let localSData=localStorage.getItem('pestData');
            localSData=JSON.parse(localSData);
            state[data.type][data.num].petsMaxData=localSData[data.type][data.num].petsMaxData;
        if(data.type=="petsStyleData"){
           
            let like= state[data.type][data.num].petsMaxData[data.listNum].Collection;
            like=!like;
            state[data.type][data.num].petsMaxData[data.listNum].listType=data.num;
            state[data.type][data.num].petsMaxData[data.listNum].saveAddNum=data.listNum;
            // localSData[data.type][data.num].petsMaxData[data.listNum];
            
            state[data.type][data.num].petsMaxData[data.listNum].Collection=like;
            if(data.isflag){
                state[data.type][data.num].petsMaxData[data.listNum].Collection=false;
            }
           
            localSData[data.type][data.num].petsMaxData[data.listNum]=state[data.type][data.num].petsMaxData[data.listNum];
            
            
        }else{
            let like2= state[data.type][data.num].Collection;
            like2=!like2;
            state[data.type][data.num].listType=data.num;

            if(data.isflag){
                state[data.type][data.num].Collection=false;
            }
            localSData[data.type][data.num]=state[data.type][data.num];
            state[data.type][data.num].Collection=!state[data.type][data.num].Collection;
        }
        localStorage.setItem('pestData',JSON.stringify(localSData));
        
    },
    //添加显示数据
    addSearchDataShow(state,data){
        state.searchDataShow=data;
    }

}