import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//导入用于设置rem 基准值模块
import 'lib-flexible'

import axios from 'axios'
import VueAxios from 'vue-axios'

//注册axios
Vue.use(VueAxios, axios)
// 配置请求基础路径
axios.defaults.baseURL = 'http://api.tianapi.com/txapi/pet/index'
//设置请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'


// 发起请求之前处理
axios.interceptors.request.use(config=>{
  if(config.method=='post'){
    let paramsString = '';
    //post请求参数序列化，转成一个字符串
    for (let key in config.data) {
      paramsString += key + '=' + config.data[key] + '&';
    }

    config.data = paramsString.slice(0, -1);
  }
  return config;
})

//添加appkey属性保存appkey
Vue.prototype.appkey = '2e2738d15e043980e7fb169172f87002';

import {
  NavBar,
  Field,
  Button,
  Form,
  CellGroup,
  Toast,
  Dialog,
  Tabbar,
  TabbarItem,
  Search,
  Swipe,
  Lazyload,
  SwipeItem,
  Stepper,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Grid,
  GridItem,
  Icon,
  Tabs,
  Tab,
  List,
  Uploader,
  DropdownMenu,
  DropdownItem,
  ShareSheet,
  Popup,
  Cell
} from 'vant'


Vue
.use(NavBar)
.use(Field)
.use(Button)
.use(Form)
.use(CellGroup)
.use(Toast)
.use(Dialog)
.use(Tabbar)
.use(TabbarItem)
.use(Search)
.use(Swipe)
.use(Lazyload)
.use(SwipeItem)
.use(Stepper)
.use(GoodsAction)
.use(GoodsActionIcon)
.use(GoodsActionButton)
.use(Grid)
.use(GridItem)
.use(Icon)
.use(Tabs)
.use(Tab)
.use(List)
.use(Uploader)
.use(DropdownMenu)
.use(DropdownItem)
.use(ShareSheet)
.use(Popup)
.use(Cell)

Vue.config.productionTip = false



new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
