export const routes = [
    {
      path: '/register',
      name: 'Register',
      component: () => import('../views/Register.vue')
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('../views/Login.vue')
    },
    {
      path: '/main',
      name: 'Main',
      component: () => import('../views/Main.vue'),
      children: [
        {
          path: 'homePage',
          name: 'HomePage',
          component: () => import('../views/mainViews/HomePage.vue')
        },
        {
          path: 'classIfiCation',
          name: 'ClassIfiCation',
          component: () => import('../views/mainViews/ClassIfiCation.vue')
        },
        {
          path: 'find',
          name: 'Find',
          component: () => import('../views/mainViews/Find.vue')
        },
        {
          path: 'my',
          name: 'My',
          component: () => import('../views/mainViews/My.vue')
        }

      ]
    },
    {
      path: '/detail/:data?',
      name: 'Detail',
      component: () => import('../views/Detail.vue')
    },
    {
      path: '/postText/:key?',
      name: 'PostText',
      component: () => import('../views/PostText.vue')
    },
    {
      path: '/releasePost',
      name: 'ReleasePost',
      component: () => import('../views/ReleasePost.vue')
    },
    {
      path: '/userManage',
      name: 'UserManage',
      component: () => import('../views/UserManage.vue')
    },
    {
      path: '/modifyPW',
      name: 'ModifyPW',
      component: () => import('../views/ModifyPW.vue')
    },
    {
      path: '/collectionPost',
      name: 'CollectionPost',
      component: () => import('../views/CollectionPost.vue')
    },
    {
      path: '/losePW',
      name: 'LosePW',
      component: () => import('../views/LosePW.vue')
    },
    {
      path: '/upLosePW',
      name: 'UpLosePW',
      component: () => import('../views/UpLosePW.vue')
    },
    {
      path: '/myAllSearch',
      name: 'MyAllSearch',
      component: () => import('../views/MyAllSearch.vue')
    },
    {
        path: '*',
        redirect: {
            name: 'Register'
        }
    }
  ]